document.addEventListener('DOMContentLoaded', () => { // Структура страницы загружена и готова к взаимодействию

  const button = document.querySelector('.header__input'); // находим кнопку для открытия/закрытия окна навигации
  const nav = document.querySelector('.ppp');

  button.addEventListener('click', () => { // при клике на кнопку
    nav.classList.add('toHide')
    button.classList.add('header__input-active') // открываем/закрываем окно навигации, добаляя/удаляя активный класс
  })

  window.addEventListener('click', e => { // при клике в любом месте окна браузера
    const target = e.target // находим элемент, на котором был клик
    if (!target.closest('.nav') && !target.closest('.header__input')) { // если этот элемент или его родительские элементы не окно навигации и не кнопка
      nav.classList.remove('toHide') 
      button.classList.remove('header__input-active')// то закрываем окно навигации, удаляя активный класс
    }
  })

})


