const projectTab = document.querySelectorAll('.project__tab');
const projectTabsItems = document.querySelectorAll('.project__tab-content');


projectTab.forEach(e => {
  e.addEventListener('click', function(){
    let currentBtn = e;
    let tabId = currentBtn.getAttribute('data-tabs');
    let currentTab = document.querySelector(tabId);


    projectTab.forEach(item => {
      item.classList.remove('active');
    })
    projectTabsItems.forEach(item => {
      item.classList.remove('active');
    })
  
    currentBtn.classList.add('active');
    currentTab.classList.add('active');
    
  })
})


